$font-family:'Raleway', sans-serif;

.subscribe {
    min-height: 500px;
    background-image: url(../web/image/img_subscribe/subscribe.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: $font-family;
    font-weight: 300;
}

.subscribe__title{
    min-width: 115px;
    max-width: 550px;
    margin-left: 10%;
    margin-bottom: 15px;
    padding-top: 145px;
    h2 {
        font-size: 40px;
        font-weight: 700;
        margin-bottom: 15px;
    }

    p{
        font-size: 17px;
        min-height: 50px;
    }
}

.subscribe__input{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10%;


    input {
        width: 570px;
        min-width: 240px;
        height: 55px;
        border: 1px solid #000;
        padding-left: 10px;
        margin-right: 3%;
        margin-bottom: 15px;
        font-size: 15px;
        background-color: 
        rgba(255, 255, 255, 0.9);
    }
    
    button {
        height: 55px;
        width: 170px;
        background-color: #96b92c;
        color: #fff;
        border: transparent;
        box-shadow: 0px 2px 8px  rgb(58, 58, 58);
        margin-bottom: 55px;
        font-size: 20px;
        transition-duration: 0.7s;
        &:hover {
            transform: rotateX(360deg) skewX(-15deg);
            background-color:#1e2434 ;
            cursor: pointer;
            border-bottom-right-radius: 25px;
            border-top-left-radius: 25px;
        }
    }
}