.blog {
    margin: 0px auto;
    margin-bottom: 0px;
    width: 100%;
    text-align: center;
    background-color:#b07cf7 ;

    &__header {
        &--title {
            font-size: 90px;
            font-family: 'Raleway', sans-serif;
            font-weight:700;
            text-align: center;
            padding-top: 100px;
            margin-top: 0px;
            margin-bottom: 30px;
        }

        &--text{
            display: block;
            max-width: 570px;
            min-width: 270px;
            height: 75px;
            max-height: 140px;
            margin: 0 auto;
            text-align: center;
            font-size: 17px;
            font-family: 'Raleway', sans-serif;
            font-weight: 300;
            opacity: 0.9;
        }
    }
}

.blog__main {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 40px;

    .blog__image {
        position: relative;
        margin: 10px 15px;
        color: #ffffff;
        min-width: 290px;
        

        &:hover {
            cursor:pointer;
            .data {
                 box-shadow:inset 0px 0px 50px 8px #f72b3b;
                color: #000; 
                }
        }

        img {
            width: 100%;
            box-shadow: 0 5px 10px 1px rgba(43, 43, 43, 0.616);
        }

    }
}

.data {
    position: absolute;
    top: 6%;
    left: 6%;
    background-color: rgba(46, 46, 46, 0.603);
    width: 60px;
    height: 60px;
    border-radius: 5px;
    transition-duration: 0.1s;

    .top {
        position: absolute;
        top: -1px;
        left: 13px;
        font-size: 32px;
        font-family: 'Raleway', sans-serif;
        font-weight: 700;
    }

    .bottom {
        position: absolute;
        font-family: 'Raleway', sans-serif;
        font-size: 21px;
        font-weight: 900;
        top: 34px;
        left: 7px;
    }
}

.blog__image {
    p {
        position: absolute;
        left: 6%;
        bottom: 17%;
        font-size: 20px;
        font-family: 'Raleway', sans-serif;
        font-weight: 700;
        text-align: left;
        }

    .smalltext {
        position: absolute;
        left: 6%;
        bottom: 8%;
        opacity: 0.7;
        font-size: 13px;
        font-family: 'Raleway', sans-serif;
        font-weight: 300;
        }
        
}




@media screen and (max-width:410px) {
    .blog__image{    
            p {
            font-size: 14px;
            .smalltext {
            font-size: 10px;
            }
        }
    }
    .blog {
        margin: 0px auto;
        margin-bottom: 0px;
        width: 100%;
        text-align: center;
        background-color:#b07cf7 ;
    
        &__header {
            &--title {
                font-size: 60px;
                padding-top: 30px;
            }
    
            &--text{
                font-size: 14px;
            }
        }
    }
}

.blog__button {
    background-color: #f72b3b;
    border: 1px solid #f72b3b;
    width: 200px;
    height: 55px;
    margin-top: 40px;
    margin-bottom: 120px;
    padding: 0;
    font-size: 20px;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    color: #fff;
    box-shadow: 0px 2px 5px rgba(43, 43, 43, 0.616);

    &:hover{
        cursor: pointer;
        transform: scale(1.2);
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
    }

    &:hover + .experimental{
        display: block;
        animation-name: move ;
          animation-duration: 1s;
          animation-iteration-count: infinite;
    }

    &:focus+ .experimental{
        display: block;
    transform: rotate(360deg);
    transition-duration: .9s;
    transition-timing-function: ease-in-out;
    }
}

.experimental{
    position:relative;
    margin: 0 auto;
    width: 0;
    height: 0;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    border-top: 60px solid red;
    display: none;
    margin-top: -80px;
    padding-bottom: 20px;   
}

@keyframes move {
    0% {top:0px;}
    25% {top:7px;}
    50% {top:14px;}
    75% {top:7px;}
    100% {top:0px;}
}

