.contact {
    background-image: url("../web/image/img_footer/img.jpg");
    background-size: cover;
    background-position: center ;
    min-height: 866px;

    &__title {
        text-align: center;
        padding-top: 100px;
        font-family: 'Raleway', sans-serif;

        &--title {
            margin: 0 auto;
            font-size: 90px;
            font-weight: 700;
        }

        &--text {
            margin: 45px auto;
            margin-top: 35px;
            font-size: 17px;
            font-weight: 300;
            max-width: 350px;
            min-height: 45px;
        }
        
    }

}

.contactflex {
    display: flex;
    flex-flow: row wrap-reverse;
    justify-content:space-around;
    align-items: baseline;
    width: 90%;
    margin: 0 auto;
}
.contact__form {
    margin: 0;
    margin-top: 10px;
    margin-right: 25px;
    width: 100%;
    max-width: 500px;
    min-width: 250px;
}

#contactform {
    width: 100%;
}

.contact__list {
    list-style-type:none;
    margin-top: 0px;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    font-size: 15px;
    color: #fff;

    &--item {
        padding-bottom: 15px;

        input {
            width: 100%;
            background-color: #00c4c45d;
            border: transparent;
            height: 50px;
            color: #fff;
            padding-left: 10px;

            &:hover {
                box-sizing: border-box;
                border-left: 4px solid #355b82;
                background-color: #00c4c4;
                font-weight: 700;
            }

            &::placeholder {
                color: #fff;
            }
        }

        textarea {
            width: 100%;
            height: 250px;
            background-color:#00c4c45d ;
            color: #fff;
            padding-left: 10px;
            resize: none;

            &:hover {
                box-sizing: border-box;
                border-left: 4px solid #355b82;
                background-color: #00c4c4;
                font-weight: 700;
            }

            &::placeholder {
                color: #ffffff;
            }
        }
    }
}

.contact__button {
    width: 200px;
    height: 50px;
    background-color: #00c4c4;
    color: #fff;
    border: transparent;
    font-family: 'Pacifico', cursive;
    font-size: 25px;
    margin-bottom: 60px;

    &:hover {
        transform: translate(8px, 5px);
        text-shadow: 4px 4px 5px #262626;
        box-shadow: -4px -4px 15px #262626;
        cursor: pointer;
    }
}

.contact__contacts {
    font-family: 'Raleway', sans-serif;
    margin-left: 15px;

    %h2 {
        margin: 0 auto;
        margin-bottom: 5px;
        font-size: 17px;
        font-weight: 700;
    }

    h2 {
        margin: 0 auto;
        margin-bottom: 5px;
        font-size: 17px;
        font-weight: 700;
    }

    p{
        @extend %h2;
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 300;
    }
}

footer {
    background-color: #262626;
    font-family: 'Raleway', sans-serif;
    color: #b9b9b9;
    min-height: 90px;
    display: flex;
    align-items: center;
    justify-content:space-around;

    .footer__copy{
        font-size: 16px;
        font-weight: 300;
    }

    .footer__logo{
        font-family: 'Pacifico', cursive;
        font-size: 25px;
    }

    .footer__author{
        font-size: 15px;
        font-weight: 300;

        .red {color: red;}
    }
}
 
@media screen and (max-width:942px) {
    
    
    
    .contact__title--title {
        font-size: 65px;
    }
   
    .contact__form {

        margin-right: 0;

    }

    .contact__contacts {
        h2 {
            font-size: 14px;
        }
    
        p{
            font-size: 17px;
        }
    }


    .contact__button {
        display: block;
        width: 145px;
        font-size: 20px;
        margin: 0 auto;
        margin-bottom: 60px;
        
    }

    footer {
        justify-content:space-around;
        
        .footer__copy{
            font-size: 14px;
            width: 110px;
            margin-left: 10px;
        }
    
        .footer__logo{
            font-size: 24px;
            color: rgba(240, 0, 0, 0.9);
            margin-right: 10px;
        }
    
        .footer__author{
            display: none;
        }
    }
     

}
