$font-family:'Raleway', sans-serif;

.gallery {
    min-height: 1530px;
    background-color: #ffe92f;
    font-family: $font-family;
    text-align: center;

    &__title {
        min-height:  330px;
        color: #000;
        max-width: 580px;
        margin: 0 auto;
        &--title {
            font-size: 90px;
            font-weight: 700;
            padding-top: 100px;
            padding-bottom: 25px;
        }
        &--text {
            font-size: 17px;
            font-weight: 300;
            width: 100%;
            min-width: 250px;
            margin: 0 auto;
        }
    }
    button {
        width: 200px;
        height: 55px;
        background-color: #5585a2;
        color: #fff;
        border: transparent;
        font-size: 20px;
        font-weight: 300;
        margin-bottom: 50px;
        box-shadow: 0px 2px 7px #333333;
        transition-duration: 0.7s;

        &:hover{
            transform: rotateX(360deg) skewX(-15deg);
            background-color:#1e2434 ;
            cursor: pointer;
            border-bottom-right-radius: 25px;
            border-top-left-radius: 25px;

        }
    }
}

.gallery__container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    img {
        width: 100%;
    }

    &top {
        display: flex;
        flex-flow: inherit;
        justify-content: center;
    }

    &bottom {
       margin-bottom: 20px;
       display: flex;
       flex-wrap: wrap;
       justify-content: center;
       align-items: center;
       div {
           margin: 10px;
       }
    }

   
}


.gallery__containertop {
    display: flex;
    flex-flow: inherit;
    justify-content: center;
   div {
       margin: 10px;
   }

    .gallery__containertop--small {
        margin: 0px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-grow: 1;
    }
}


@media screen and (max-width: 1326px  ) {
    .gallery__containertop{
        .gallery__containertop--small {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        flex-grow: 1;
        }
    }
    .gallery__containerbottom {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img {
            width:769px;
        }
    }
}

@media screen and (max-width: 920px  ) {
    .gallery__containertop, .gallery__containerbottom{
        img{
            width: 450px;
        }
    }
    
}

@media screen and (max-width: 520px  ) {
    .gallery__containertop, .gallery__containerbottom{
        img{
            width: 290px;
        }
    }
    
    
    .gallery {
        &__title {
            min-height: 240px;
            &--title {
                padding-top: 50px;
            font-size: 60px;
            }
            &--text {
            font-size: 14px;
            min-width: 300px;
            }
        }
    }
}