$font-family:'Raleway', sans-serif;
  
.ourserv {
    min-height: 790px;
    background-color: #70bfff;
    font-family: $font-family;
    font-weight: 700;
    font-size: 20px;
    display: flex;
    flex-flow: row wrap-reverse;
    justify-content: space-around;
    align-items: center;
    padding: 40px;
    &__ul {
        max-width: 400px;
        min-height: 465px;
        list-style-type:none;
        margin: 0 auto;
        margin-top: 60px;
    }

}

.ourserv__li {
    background-color: #fff;
    margin-bottom: 20px;
    min-height: 50px;
    transition-duration: 0.4s;
    .ourserv__bgdiv {
        width: 75%;
        height: 100%;
        border-right: 1px solid #000;
        padding: 38px;

        &--padding {
            padding: 27px;
        }

        p{
        text-align: center;
           padding: 0; }

    }
    .ourserv__smdiv {
        width: 25%;
        height: 100%;
        float: right;
        text-align: center;
        padding-top: 23px;
        
        &--padding {
            padding-top: 29px;
        }
        
        
    }
    &:hover {
        transform: translateX(-15px);
        box-shadow: 
        3px 0px 1px  rgba(194, 194, 194, 0.555),
        8px 0px 1px  rgba(116, 116, 116, 0.555),
        13px 0px 0px rgb(63, 63, 63);
         
        cursor: pointer;
    }
}



.ourserv__title {
    background-image: url("../web/image/img_ourservices/tomato.png");
    background-repeat: no-repeat;
    background-size: contain;
    height: 610px;
    min-height: 300px;
    margin: 0 auto;

    .ourserv__title--h2 {
        font-size: 90px;
        color:#fff;
        margin-top: 37%;
    }
}


@media screen and (max-width:640px) {
    .ourserv__title {
    width: 100%;
    height: 400px;
        .ourserv__title--h2 {
            font-size: 60px;
text-align: center;
        }
    }
}